@import "../../styles/constants.scss";

.wrapper {
    opacity: 1;
  .container {
    .heading {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      p {
        line-height: 30px;
      }

      @media (max-width: $sm ), (max-width: $md) {
        .heading {
          display: block;
        }
      }
    }

    .comments {
      // fix for spacing between slides
      .slider :global {
        .slick-list {
          margin: 0 -7px;
          box-sizing: initial;
          padding: 80px 0px;
          overflow-y: visible;
          & .slick-slide > div {
            padding: 0 20px;
          }
          & .slick-slide.slick-active.slick-current + .slick-slide {
            @media (min-width: $lg) {
              transition: all 1000ms ease-out;
              scale: 1.1;
              transform: translateY(-30px);
            }
          }
        }
      }

      .comment {
        box-shadow: rgb(0 0 0 / 5%) -3px 11px 13px 0px;
        padding: 2rem;
        display: flex !important;
        flex-direction: column;
        gap: 1rem;
        background: white;
        border-radius: 20px;
        position: relative;
        margin-top: 10%;
        img {
          margin: auto;
          border-radius: 100%;
          width: 4.5rem;
          height: 4.5rem;
          position: absolute;
          top: -2rem;
          filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.08));
        }
        p {
          text-align: center;
          font-size: 0.9rem;
          line-height: 25px;
          margin-top: 4%;
        }
        .line {
          width: 100%;
          border: 0.2px solid rgb(234, 234, 234);
        }
        .bio {
          > * {
            display: block;
            text-align: center;
          }
          > :nth-child(1) {
            font-weight: 600;
            margin-bottom: 5px;
          }
          > :nth-child(2) {
            font-size: 0.7rem;
            color: rgb(172, 172, 172);
          }
        }
      }
    }
  }
}
